import axios from "axios";
import { useContext, useState } from "react";
import { Conte } from "../App";

export const Search = () => {
  const [txt, set_txt] = useState("");

  const [data, setdata] = useState({});
  const { addItem } = useContext(Conte);

  const search = async (text) => {
    await axios
      .get(`http://127.0.0.1:8000/products_search/${text}/`)
      .then((res) => {
        // console.log(res);
        setdata(res.data.results);
      })
      .catch((err) => console.log(err));
  };
  return (
    <div className="container-fluid pt-5 pb-3">
      <form
        onSubmit={(e) => {
          e.preventDefault();

          search(txt);
        }}
      >
        <div className="input-group">
          <input
            type="text"
            className="form-control"
            placeholder="Search for products"
            value={txt}
            required
            onChange={(e) => set_txt(e.target.value)}
          />
          <div className="input-group-append">
            <button
              type="submit"
              className="input-group-text bg-transparent text-primary"
            >
              <i className="fa fa-search" />
            </button>
          </div>
        </div>
      </form>

      <h2 className="section-title position-relative text-uppercase mx-xl-5 mb-4">
        {data.length > 0 ? (
          <span className="bg-white pr-3"> Products</span>
        ) : (
          <></>
        )}
      </h2>
      <div className="row px-xl-5">
        {/* <p> {data}</p> */}
        {console.log(data)}
        {data.length > 0 ? (
          Object.entries(data).map(([key, val], i) => (
            <div
              id={Math.random().toString(16).slice(2)}
              className="col-lg-3 col-md-4 col-sm-6 pb-1"
            >
              <div id={val.id} className="product-item bg-light mb-4">
                <div className="product-img position-relative overflow-hidden">
                  <img
                    className="img-fluid w-100"
                    src={"media/" + val.image}
                    alt=""
                  />

                  <div className="product-action">
                    <button
                      className="btn btn-outline-dark btn-square"
                      onClick={() => addItem(val.id)}
                    >
                      <i className="fa fa-shopping-cart"></i>
                    </button>
                  </div>
                </div>
                <div className="text-center py-4">
                  <a className="h6 text-decoration-none text-truncate" href="/">
                    {val.name}
                  </a>
                  <div className="d-flex align-items-center justify-content-center mt-2">
                    <h5>${val.price}</h5>
                    {/* <h6 className="text-muted ml-2">
                        <del>$123.00</del>
                      </h6> */}
                  </div>
                  <div className="d-flex align-items-center justify-content-center mb-1">
                    {[...Array(val.star).keys()].map((d) => (
                      <small
                        id={Math.random().toString(16).slice(2)}
                        className="fa fa-star text-primary mr-1"
                      ></small>
                    ))}
                    {[...Array(5 - val.star).keys()].map((d) => (
                      <small
                        id={Math.random().toString(16).slice(2)}
                        className="far fa-star text-primary mr-1"
                      ></small>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          ))
        ) : (
          <h1>No Results</h1>
        )}
      </div>
    </div>
  );
};
