import axios from "axios";
import { useContext, useEffect, useState } from "react";
import { NavLink, useParams } from "react-router-dom";
import { Conte } from "../App";

export const Detail = () => {
  const { user, addItem } = useContext(Conte);
  const { idd } = useParams();
  const [porduct, setporduct] = useState(null);
  // console.log(idd, 898989989);
  const getData = async () => {
    await axios
      .get(`http://127.0.0.1:8000/detail/${idd}/`)
      .then((res) => {
        // console.log(res);
        setporduct(res.data);
      })
      .catch((er) => console.log(er));
  };

  // getData();
  useEffect(() => {
    getData();
  }, []);

  return (
    <>
      <div className="container-fluid">
        <div className="row px-xl-5">
          <div className="col-12">
            <nav className="breadcrumb bg-light mb-30">
              <NavLink className="breadcrumb-item text-dark" to="/">
                Home
              </NavLink>
              <NavLink className="breadcrumb-item text-dark" to="/shop">
                Shop
              </NavLink>
              <span className="breadcrumb-item active">Shop Detail</span>
            </nav>
          </div>
        </div>
      </div>
      {porduct && (
        <div className="container-fluid pb-5">
          <div className="row px-xl-5">
            <div className="col-lg-5 mb-30">
              <img
                className="w-100 h-100"
                src={"/media/" + porduct.image}
                alt=""
              />
            </div>
            <div className="col-lg-7 h-auto mb-30">
              <div className="h-100 bg-light p-30">
                <h3>{porduct.name}</h3>
                <div className="d-flex mb-3">
                  <div className="text-primary mr-2">
                    {[...Array(porduct.star).keys()].map((d) => (
                      <small
                        id={Math.random().toString(16).slice(2)}
                        className="fa fa-star text-primary mr-1"
                      ></small>
                    ))}
                    {5 - porduct.star
                      ? [...Array(5 - porduct.star).keys()].map((d) => (
                          <small
                            id={Math.random().toString(16).slice(2)}
                            className="far fa-star text-primary mr-1"
                          ></small>
                        ))
                      : null}
                  </div>
                </div>
                <h3 className="font-weight-semi-bold mb-4">${porduct.price}</h3>
                <p className="mb-4">{porduct.description}</p>

                <div className="d-flex align-items-center mb-4 pt-2">
                  <button
                    onClick={() => addItem(porduct.id)}
                    className="btn btn-primary px-3"
                  >
                    <i className="fa fa-shopping-cart mr-1"></i> Add To Cart
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};
