import { useContext, useEffect, useState } from "react";
import { Conte } from "../App";
import axios from "axios";
import "../asset/css/login.css";

export const Login = () => {
  const [islogin, setislogin] = useState(false);
  const [emails, set_email] = useState("");
  const [f_data, set_f_data] = useState({});
  const [load, setload] = useState({});
  const [passs, set_pass] = useState("");
  const { log_in, signup, googleAuthenticate } = useContext(Conte);
  const { email, pass, name } = f_data;
  const { in_, upp } = load;
  const onchange = (e) =>
    set_f_data({ ...f_data, [e.target.name]: e.target.value });

  const con_google = async () => {
    await axios
      .get(
        `http://127.0.0.1:8000/auth/o/google-oauth2/?redirect_uri=http://127.0.0.1:8000/google`
      )
      .then((res) => {
        // console.log(res.data.authorization_url);
        window.location.replace(res.data.authorization_url);
      });
  };

  // useEffect(() => {
  //   con_google();
  // }, [location]);
  return (
    <>
      <div className="wrapper">
        {islogin ? (
          <div className="card">
            <article className="card-body">
              <button
                onClick={() => setislogin(false)}
                className="float-right btn btn-outline-primary"
              >
                Sign up
              </button>
              <h4 className="card-title mb-4 mt-1">Sign in</h4>

              <button
                onClick={con_google}
                className="btn btn-block btn-outline-info"
              >
                <i className="fab fa-google"></i>  Sign up via Google
              </button>

              <hr />
              <form
                onSubmit={(e) => {
                  e.preventDefault();
                  setload({ ...load, in_: true });
                  log_in(emails, passs);
                  setload({ ...load, in_: false });
                }}
              >
                <div className="form-group">
                  <input
                    onChange={(e) => set_email(e.target.value)}
                    value={emails}
                    className="form-control"
                    placeholder="Email or login"
                    type="email"
                    required
                  />
                </div>
                <div className="form-group">
                  <input
                    onChange={(e) => set_pass(e.target.value)}
                    value={passs}
                    className="form-control"
                    placeholder="******"
                    type="password"
                    required
                  />
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <div className="form-group">
                      <button type="submit" className="btn btn-dark  btn-block">
                        {in_ ? "....." : "login"}
                      </button>
                    </div>
                  </div>
                </div>
              </form>
            </article>
          </div>
        ) : (
          <div className="card">
            <article className="card-body">
              <button
                onClick={() => setislogin(true)}
                className="float-right btn btn-outline-primary"
              >
                Sign in
              </button>
              <h4 className="card-title mb-4 mt-1">Sign up</h4>
              <button
                onClick={con_google}
                className="btn btn-block btn-outline-info"
              >
                <i className="fab fa-google"></i>  Sign in via Google
              </button>
              <hr />
              <form
                onSubmit={(e) => {
                  e.preventDefault();
                  setload({ ...load, upp: true });
                  signup(f_data);
                  setload({ ...load, upp: false });
                }}
              >
                <div className="form-group">
                  <input
                    name="name"
                    value={name}
                    onChange={(e) => onchange(e)}
                    className="form-control"
                    placeholder="Name"
                    type="text"
                    required
                  />
                </div>
                {/* 
                <div className="form-group">
                  <input
                    name="num"
                    onChange={(e) => onchange(e)}
                    className="form-control"
                    placeholder="Phone Number"
                    type="text"
                  />
                </div> */}

                <div className="form-group">
                  <input
                    name="email"
                    value={email}
                    onChange={(e) => onchange(e)}
                    className="form-control"
                    placeholder="Email"
                    type="email"
                    required
                  />
                </div>

                <div className="form-group">
                  <input
                    name="pass"
                    value={pass}
                    onChange={(e) => onchange(e)}
                    className="form-control"
                    placeholder="******"
                    type="password"
                    required
                  />
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <div className="form-group">
                      <button type="submit" className="btn btn-dark btn-block">
                        {upp ? "....." : "Sign up "}
                      </button>
                    </div>
                  </div>
                </div>
              </form>
            </article>
          </div>
        )}
      </div>
    </>
  );
};
