import axios from "axios";
import { useContext, useEffect, useState } from "react";
import { Conte } from "../App";
import { NavLink } from "react-router-dom";
export const Cart = () => {
  const { user } = useContext(Conte);
  const [data, setdata] = useState([]);
  let cc = 0;
  const getItem = async () => {
    await axios
      .get(`http://127.0.0.1:8000/cart/${user.id}/`)
      .then((res) => {
        // console.log(res.data);
        setdata(res.data);
      })
      .catch((er) => {
        console.log(er);
      });
  };

  const deleteItem = async (c) => {
    // console.log(c);
    await axios.delete(`http://127.0.0.1:8000/cart/${user.id}/${c}/`);
    setdata(data.filter((a) => a.item.id !== c));
    window.location.href = "/cart";
  };

  useEffect(() => {
    getItem();
  }, [user]);

  return (
    <>
      <div className="container-fluid">
        <div className="row px-xl-5">
          <div className="col-12">
            <nav className="breadcrumb bg-light mb-30">
              <NavLink className="breadcrumb-item text-dark" to="/">
                Home
              </NavLink>
              <NavLink className="breadcrumb-item text-dark" to="/shop">
                Shop
              </NavLink>
              <span className="breadcrumb-item active">Shopping Cart</span>
            </nav>
          </div>
        </div>
      </div>

      <div className="container-fluid">
        <div className="row px-xl-5">
          <div className="col-lg-8 table-responsive mb-0">
            <table className="table table-light table-borderless table-hover text-center mb-0">
              <thead className="thead-dark">
                <tr>
                  <th>Products</th>
                  <th>Total</th>
                  <th>Remove</th>
                </tr>
              </thead>
              <tbody className="align-middle">
                {data && data.length > 0 ? (
                  data.map((items) => {
                    cc += parseInt(items.item.price);
                    return (
                      <tr
                        id={
                          Math.random().toString(16).slice(2) + items.item.price
                        }
                      >
                        <td className="align-middle">
                          <img
                            src={items.item.image}
                            alt=""
                            style={{ width: " 45px" }}
                          />
                          {items.item.name}
                        </td>

                        <td className="align-middle"> {items.item.price}</td>
                        <td className="align-middle">
                          <button
                            onClick={() => deleteItem(items.item.id)}
                            className="btn btn-sm btn-danger"
                          >
                            <i className="fa fa-times"></i>
                          </button>
                        </td>
                      </tr>
                    );
                  })
                ) : (
                  <tr id={Math.random().toString(16).slice(2) + "pp"}>
                    <td> Empty Cart</td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
          <div className="col-lg-4">
            <h5 className="section-title position-relative text-uppercase mb-3">
              <span className="bg-white pr-3">Cart Summary</span>
            </h5>
            <div className="bg-light p-30 mb-5">
              <div className="pt-2">
                <div className="d-flex justify-content-between mt-2">
                  <h5>Total</h5>
                  <h5>${cc}.00</h5>
                </div>
                <button className="btn btn-block btn-dark  font-weight-bold my-3 py-3">
                  <NavLink className="nav-item nav-link" to="/checkout">
                    Proceed To Checkout
                  </NavLink>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
