import axios from "axios";
import { useContext, useState } from "react";
import { Conte } from "../App";
import { NavLink } from "react-router-dom";
export const Post = () => {
  const { user } = useContext(Conte);
  const [name, setname] = useState("");
  const [price_, setprice] = useState("");
  const [cate, setcate] = useState("");
  const [img, setimg] = useState(null);
  const [upl, setupl] = useState(0);
  const [address, setaddress] = useState("");
  const post = async (c) => {
    console.log(img);
    setupl(1);
    await axios
      .post(
        `http://127.0.0.1:8000/post/`,
        {
          id: user.id,
          price: price_,
          description: address,
          category: cate,
          name: name,
          img: img,
        },
        {
          headers: {
            "content-type": "multipart/form-data",
          },
        }
      )
      .then(() => {
        alert("done");
        window.location.href = "/shop";
      })
      .catch((er) => {
        setupl(0);
        console.log(er);
      });
  };

  return (
    <>
      <div className="container-fluid">
        <div className="row px-xl-5">
          <div className="col-12">
            <nav className="breadcrumb bg-light mb-30">
              <NavLink className="breadcrumb-item text-dark" to="/">
                Home
              </NavLink>
              <NavLink className="breadcrumb-item text-dark" to="/shop">
                Shop
              </NavLink>
              <span className="breadcrumb-item active">Post</span>
            </nav>
          </div>
        </div>
      </div>
      <div className="container-fluid">
        <div className="row px-xl-5">
          <div className="col-lg-8">
            <h5 className="section-title position-relative text-uppercase mb-3">
              <span className="bg-white pr-3">Product Info</span>
            </h5>
            <div className="bg-light p-30 mb-5">
              <div className="row">
                <div className="col-md-6 form-group">
                  <label>Name</label>
                  <input
                    className="form-control"
                    type="text"
                    placeholder="John"
                    value={name}
                    required
                    onChange={(e) => setname(e.target.value)}
                  />
                </div>

                <div className="col-md-6 form-group">
                  <label>Price</label>
                  <input
                    className="form-control"
                    type="number"
                    placeholder="100"
                    value={price_}
                    required
                    onChange={(e) => setprice(e.target.value)}
                  />
                </div>
                <div className="col-md-6 form-group">
                  <label>Description </label>
                  <input
                    className="form-control"
                    type="text"
                    placeholder="123 Street"
                    value={address}
                    required
                    onChange={(e) => setaddress(e.target.value)}
                  />
                </div>
                <div className="col-md-6 form-group">
                  <label>Image </label>
                  <input
                    className="form-control"
                    type="file"
                    accept="image/png, image/jpeg"
                    onChange={(e) => (
                      setimg(e.target.files[0]), console.log(e.target.files)
                    )}
                  />
                </div>
                <div className="col-md-6 form-group">
                  <label>Category </label>

                  <select
                    onChange={(e) => setcate(e.target.value)}
                    name="ss"
                    className="form-control"
                    id="dd"
                  >
                    <option selected value="select"></option>
                    <option value="1">Fashion</option>
                    <option value="2">Cosmotics</option>
                    <option value="3">Shoes</option>
                    <option value="4">Electronics</option>
                  </select>
                </div>
              </div>
              <div className="row">
                <div className="col-md-6 form-group">
                  <button
                    onClick={post}
                    className="btn btn-block btn-dark  font-weight-bold my-3 py-3"
                  >
                    {upl === 0 ? "Submit" : "........."}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
