import "./asset/lib/animate/animate.min.css";
import "./asset/lib/owlcarousel/assets/owl.carousel.min.css";
// import $ from "jquery";
import "./asset/lib/owlcarousel/owl.carousel.min.js";
import "./asset/lib/easing/easing.min.js";
import "./asset/js/main.js";

import "./asset/css/loading.scss";
import "./asset/css/style.css";
import "./asset/css/bootstrap.css";
import { Nav } from "./comp/nav";
import { Footer } from "./comp/footer";
import { Home } from "./comp/home";
import { Cart } from "./comp/cart";
import { Shop } from "./comp/shop";
import { Checkout } from "./comp/checkout";
import { Login } from "./comp/login";
import { Post } from "./comp/post";
// import jwt_decode from "jwt-decode";
import axios from "axios";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { createContext, useEffect, useState } from "react";
import Profile from "./comp/profile";
import { Detail } from "./comp/detail";
import Oauth from "./comp/oauth";
import { Search } from "./comp/search";
export const Conte = createContext();
function App() {
  const load_user = async () => {
    if (localStorage.getItem("tokens")) {
      await axios
        .get(`http://127.0.0.1:8000/auth/users/me/`, {
          headers: {
            Authorization:
              "JWT " + JSON.parse(localStorage.getItem("tokens")).access,
          },
        })
        .then((response) => {
          // console.log(response, 1234);
          setuser(response.data);
        })

        .catch((er) => {
          console.log(er, 11);
          window.location.href = "/";
        });
    } else {
      setuser({});
    }
  };
  const [user, setuser] = useState(null);
  async function googleAuthenticate(state, code) {
    const details = {
      state: state,
      code: code,
    };

    const formBody = Object.keys(details)
      .map(
        (key) =>
          encodeURIComponent(key) + "=" + encodeURIComponent(details[key])
      )
      .join("&");

    await axios
      .post(`http://127.0.0.1:8000/auth/o/google-oauth2/?${formBody}`)
      .then((res) => {
        localStorage.setItem("tokens", JSON.stringify(res.data));
        // console.log(JSON.parse(localStorage.getItem("tokens")), 555666);
        load_user(JSON.parse(localStorage.getItem("tokens")).access);

        window.location.href = "/";
      })
      .catch((er) => console.log(er));
  }

  const logout = () => {
    localStorage.removeItem("tokens");
    window.location.href = "/";
  };
  const signin = async (e, p) => {
    await axios
      .post("http://127.0.0.1:8000/auth/jwt/create/", {
        email: e,
        password: p,
      })
      .then((res) => {
        // console.log(res.data);
        setuser(res.data);

        localStorage.setItem("tokens", JSON.stringify(res["data"]));

        window.location.href = "/";
      })
      .catch((error) => {
        alert("Error Pass or Email");
      });
  };

  const signup = async ({ name, email, pass }) => {
    await axios
      .post("http://127.0.0.1:8000/auth/users/", {
        email: email,
        first_name: name,
        password: pass,
      })
      .then((res) => {
        // console.log(res/);
        window.location.href = "/login";
      })
      .catch((error) => {
        alert("Enter valid Pass or Email");
      });
  };
  const addItem = async (c) => {
    if (JSON.stringify(user) !== "{}") {
      await axios
        .post(`http://127.0.0.1:8000/cart/`, {
          item: c,
          users: user.id,
        })
        .then(() => {
          alert("done");
          window.location.href = "/";
        })
        .catch((er) => console.log(er));
    } else {
      alert("Please register first");
    }
  };

  const updater = async ({ name, passs }) => {
    // console.log(name, passs, user.user_id, 909);
    await axios
      .post("http://127.0.0.1:8000/update/", {
        name: name,
        pass: passs,
        id: user.id,
      })
      .then(async (res) => {
        // console.log(res);
        // console.log(res.data.pass);
        await axios
          .post("http://127.0.0.1:8000/auth/jwt/create/", {
            email: user.email,
            password: res.data.pass,
          })
          .then((res) => {
            // console.log(res);
            setuser(res["data"]);

            localStorage.setItem("tokens", JSON.stringify(res["data"]));

            window.location.href = "/profile";
          })
          .catch((error) => {
            console.log(error);
          });
      })
      .catch((error) => {
        alert("Error Pass or Email");
      });
  };

  let data_ = {
    user: user,
    signup: signup,
    log_in: signin,
    update: updater,
    logout: logout,
    googleAuthenticate: googleAuthenticate,
    load_user: load_user,
    addItem: addItem,
  };
  useEffect(() => {
    load_user();
  }, []);

  return (
    <>
      {user ? (
        <Conte.Provider value={data_}>
          <Router>
            <Nav />
            <Routes>
              <Route exact path="/" element={<Home />} />
              <Route exact path="shop" element={<Shop />} />
              <Route exact path="cart" element={<Cart />} />
              <Route exact path="checkout" element={<Checkout />} />
              <Route exact path="login" element={<Login />} />
              <Route exact path="profile" element={<Profile />} />
              <Route exact path="detail/:idd" element={<Detail />} />
              <Route exact path="google" element={<Oauth />} />
              <Route exact path="search" element={<Search />} />
              <Route exact path="post" element={<Post />} />
            </Routes>
            <Footer />
          </Router>
        </Conte.Provider>
      ) : (
        <div id="containerd">
          <div className="divider" aria-hidden="true"></div>
          <p className="loading-text" aria-label="Loading">
            <span className="letter" aria-hidden="true">
              L
            </span>
            <span className="letter" aria-hidden="true">
              o
            </span>
            <span className="letter" aria-hidden="true">
              a
            </span>
            <span className="letter" aria-hidden="true">
              d
            </span>
            <span className="letter" aria-hidden="true">
              i
            </span>
            <span className="letter" aria-hidden="true">
              n
            </span>
            <span className="letter" aria-hidden="true">
              g
            </span>
          </p>
        </div>
      )}
    </>
  );
}

export default App;
