import axios from "axios";
import { useContext, useState, useEffect } from "react";
import { NavLink } from "react-router-dom";
import { Conte } from "../App";

export const Nav = () => {
  const { user, logout } = useContext(Conte);
  const [count, setcount] = useState(0);
  // console.log(user, 545353);
  const g = async () => {
    if (JSON.stringify(user) !== "{}") {
      await axios
        .post("http://127.0.0.1:8000/cart_count/", {
          id: user.id,
        })
        .then((res) => {
          setcount(res.data);

          // alert(899999);
          // console.log(res.data, 777777777777777);
        })
        .catch((er) => {
          console.log(er, 99);
        });
    }
  };

  useEffect(() => {
    g();
  }, []);
  return (
    <>
      <div className="container-fluid">
        <div className="row bg-secondary py-1 px-xl-5">
          <div className="col-lg-6 text-center text-lg-right">
            <div className="d-inline-flex align-items-center d-block d-lg-none">
              {JSON.stringify(user) !== "{}" ? (
                <>
                  <NavLink className="btn px-0 ml-2" to="/profile">
                    <i className="fas fa-user  text-primary"></i>
                    <span
                      className="badge text-dark"
                      style={{ paddingbottom: "2px" }}
                    >
                      {user.first_name}
                    </span>
                  </NavLink>
                  <NavLink className="btn px-0 ml-3" to="/cart">
                    <i className="fas fa-shopping-cart text-primary"></i>
                    <span
                      className="badge text-dark border border-secondary rounded-circle"
                      style={{ paddingbottom: "2px" }}
                    >
                      {count}
                    </span>
                  </NavLink>
                  <i onClick={logout} className="btn px-0 ml-2">
                    {/* <i className="fas fa-right-from-bracket text-primary"></i> */}
                    <i className="fas fa-2x fa-sign-out-alt  text-primary"></i>
                  </i>
                </>
              ) : (
                <NavLink className="btn btn-2x px-0 ml-3" to="/Login">
                  <i className="fas fa-user text-primary"></i>
                  <span
                    className="badge text-dark  "
                    style={{ paddingbottom: "2px" }}
                  >
                    Sign in
                  </span>
                </NavLink>
              )}
            </div>
          </div>
        </div>
        <div className="row align-items-center bg-light py-3 px-xl-5 d-none d-lg-flex">
          <div className="col-lg-4">
            <a href="/" className="text-decoration-none">
              <span className="h1 text-uppercase text-dark bg-light px-2">
                
              </span>
              <span className="h1 text-uppercase text-light bg-primary px-2 ml-n1">
                
              </span>
            </a>
          </div>
          <div className="col-lg-4 col-6 text-left"></div>
          <div className="col-lg-4 col-6 text-right">
            <p className="m-0">Customer Service</p>
            <h5 className="m-0">+251 945 320 109</h5>
          </div>
        </div>
      </div>

      <div className="container-fluid bg-dark mb-30">
        <div className="row px-xl-5">
          <div className="col-lg-12">
            <nav className="navbar navbar-expand-lg bg-dark navbar-dark py-3 py-lg-0 px-0">
              <a href="/" className="text-decoration-none d-block d-lg-none">
                <span className="h1 text-uppercase text-dark bg-light px-2">
                  
                </span>
                <span className="h1 text-uppercase text-light bg-primary px-2 ml-n1">
                  
                </span>
              </a>

              <button
                type="button"
                className="navbar-toggler"
                data-toggle="collapse"
                data-target="#navbarCollapse"
              >
                <span className="navbar-toggler-icon"></span>
              </button>
              <div
                className="collapse navbar-collapse justify-content-between"
                id="navbarCollapse"
              >
                <div className="navbar-nav mr-auto py-0">
                  <NavLink className="nav-item nav-link" to="/">
                    Home
                  </NavLink>
                  <NavLink className="nav-item nav-link" to="/shop">
                    Shop
                  </NavLink>
                  <NavLink className="nav-item nav-link" to="/search">
                    Search
                  </NavLink>
                  {JSON.stringify(user) !== "{}" ? (
                    <>
                      {" "}
                      <NavLink className="nav-item nav-link" to="/cart">
                        Shopping Cart
                      </NavLink>
                      <NavLink className="nav-item nav-link" to="/post">
                        Post
                      </NavLink>
                    </>
                  ) : null}
                </div>
                <div className="navbar-nav ml-auto py-0 d-none d-lg-block">
                  {JSON.stringify(user) !== "{}" ? (
                    <>
                      <NavLink className="btn px-0 ml-2" to="/profile">
                        <i className="fas fa-user  text-primary"></i>
                        <span
                          className="badge text-white-50"
                          style={{ paddingbottom: "2px" }}
                        >
                          {user.first_name}
                        </span>
                      </NavLink>{" "}
                      <NavLink className="btn px-0 ml-3" to="/cart">
                        <i className="fas fa-shopping-cart text-primary"></i>
                        <span
                          className="badge text-secondary border border-secondary rounded-circle"
                          style={{ paddingbottom: "2px" }}
                        >
                          {count}
                        </span>
                      </NavLink>
                      <b onClick={logout} className="btn px-0 ml-3">
                        <i className="fas fa-sign-out-alt  text-primary"></i>
                        <span
                          className="badge  text-white-50"
                          style={{ paddingbottom: "2px" }}
                        >
                          Log out
                        </span>
                      </b>
                    </>
                  ) : (
                    <NavLink className="btn px-0 ml-2" to="/Login">
                      <i className="fas fa-2x fa-user text-primary"></i>
                      <span
                        className="badge  text-white-50"
                        style={{ paddingbottom: "2px" }}
                      >
                        Sign in
                      </span>
                    </NavLink>
                  )}
                </div>
              </div>
            </nav>
          </div>
        </div>
      </div>
    </>
  );
};
