import axios from "axios";
import { useContext, useEffect, useState } from "react";
import { Conte } from "../App";
import { NavLink } from "react-router-dom";
export const Checkout = () => {
  const { user } = useContext(Conte);
  const [data, setdata] = useState([]);
  const [name, setname] = useState("");
  const [phone_num, setnum] = useState("");
  const [address, setaddress] = useState("");
  let cc = 0;
  const order = async (c) => {
    await axios
      .post(`http://127.0.0.1:8000/order/`, {
        address: address,
        name: name,
        phone_num: phone_num,
        id: user.id,
      })
      .then((res) => {
        console.log(res);
        alert(res.data.txt);
        window.location.href = "/shop";
      })
      .catch((er) => console.log(er));
  };
  const getItem = async () => {
    await axios
      .get(`http://127.0.0.1:8000/cart/${user.id}/`)
      .then((res) => {
        console.log(res.data);
        setdata(res.data);
      })
      .catch((er) => {
        console.log(er);
      });
  };
  useEffect(() => {
    getItem();
    console.log(user);
  }, [user]);
  return (
    <>
      <div className="container-fluid">
        <div className="row px-xl-5">
          <div className="col-12">
            <nav className="breadcrumb bg-light mb-30">
              <NavLink className="breadcrumb-item text-dark" to="/">
                Home
              </NavLink>
              <NavLink className="breadcrumb-item text-dark" to="/shop">
                Shop
              </NavLink>
              <span className="breadcrumb-item active">Checkout</span>
            </nav>
          </div>
        </div>
      </div>
      <div className="container-fluid">
        <div className="row px-xl-5">
          <div className="col-lg-8">
            <h5 className="section-title position-relative text-uppercase mb-3">
              <span className="bg-white pr-3">Billing Address</span>
            </h5>
            <div className="bg-light p-30 mb-5">
              <div className="row">
                <div className="col-md-6 form-group">
                  <label>Full Name</label>
                  <input
                    className="form-control"
                    type="text"
                    placeholder="John"
                    value={name}
                    required
                    onChange={(e) => setname(e.target.value)}
                  />
                </div>

                <div className="col-md-6 form-group">
                  <label>Mobile No</label>
                  <input
                    className="form-control"
                    type="text"
                    placeholder="+123 456 789"
                    value={phone_num}
                    required
                    onChange={(e) => setnum(e.target.value)}
                  />
                </div>
                <div className="col-md-6 form-group">
                  <label>Address </label>
                  <input
                    className="form-control"
                    type="text"
                    placeholder="123 Street"
                    value={address}
                    required
                    onChange={(e) => setaddress(e.target.value)}
                  />
                </div>
              </div>
              <div className="row">
                <div className="col-md-6 form-group">
                  <button
                    onClick={order}
                    className="btn btn-block btn-dark  font-weight-bold my-3 py-3"
                  >
                    Submit
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-4">
            <h5 className="section-title position-relative text-uppercase mb-3">
              <span className="bg-white pr-3">Order Total</span>
            </h5>
            <div className="bg-light p-30 mb-5">
              <div className="border-bottom">
                <h6 className="mb-3">Products</h6>

                {data && data.length > 0 ? (
                  data.map((items) => {
                    cc += parseInt(items.item.price);
                    return (
                      <div className="d-flex justify-content-between">
                        <p> {items.item.name} </p>
                        <p> {items.item.price} </p>
                      </div>
                    );
                  })
                ) : (
                  <tr id={Math.random().toString(16).slice(2) + "pp"}>
                    <td> Empty Cart</td>
                  </tr>
                )}
              </div>

              <div className="pt-2">
                <div className="d-flex justify-content-between mt-2">
                  <h5>Total</h5>
                  <h5>${cc}.00</h5>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
